import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

import auth from './modules/auth'

import series from './modules/series'

Vue.use(Vuex)

export default new Vuex.Store({
	plugins: [createPersistedState()],
	modules: {
	  auth,
	  series,
	},
	state: {
	},
	getters: {
	},
	mutations: {},
	actions: {}
})



